/* eslint-disable max-len */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/label-has-associated-control */
import { yupResolver } from '@hookform/resolvers/yup';
import React from 'react';
import { Col, Row } from 'react-bootstrap';
import { Controller, FormProvider, useForm } from 'react-hook-form';
import { useMutation } from 'react-query';
import { toast } from 'react-toastify';

import Button from 'components/atoms/Button';
import Image from 'components/atoms/Image';
import Input from 'components/atoms/Input';
import TextArea from 'components/atoms/TextArea';
import Typography from 'components/atoms/Typography';
import Container from 'components/organisms/Container';
import { buyForMeService, buyForMeUnAuthService } from 'services/buyForMe';
import { BuyForMeDataRequest } from 'services/buyForMe/types';
import { useAppSelector } from 'store/hooks';
import { ERROR_MAPPING } from 'utils/constants';
import { buyForMeSchema } from 'utils/schemas';

const defaultValues: BuyForMeDataRequest = {
  fullname: '',
  email: '',
  phone: '',
  guestNote: '',
  productImages: []
};
const BuyForMe: React.FC = () => {
  const profile = useAppSelector((state) => state.auth.profile);
  const buyForMeMethod = useForm<BuyForMeDataRequest>({
    resolver: yupResolver(buyForMeSchema),
    defaultValues
  });

  const { mutate: buyForMeMutate, isLoading } = useMutation(
    ['buyForMeAction', profile],
    profile ? buyForMeService : buyForMeUnAuthService,
    {
      onSuccess: () => {
        buyForMeMethod.reset(defaultValues);
        toast.success(' gi thng tin thnh cng!', { toastId: 'submitSuccess' });
      },
      onError: (errors: any) => {
        if (errors.length > 0) {
          errors.forEach((ele: ErrorResponse) => {
            buyForMeMethod.setError(ele.field as any, { message: ERROR_MAPPING[ele.message] });
          });
        } else {
          toast.error(' c li xy ra!', { toastId: 'submitFail' });
        }
      }
    }
  );

  const onSubmit = (data: BuyForMeDataRequest) => {
    buyForMeMutate(data);
  };

  return (
    <div className="t-buyForMe">
      <Container>
        <Row>
          <Col lg={6} md={12}>
            <div className="t-buyForMe_content">
              <div className="t-buyForMe_title">
                <Typography.Heading type="h4" modifiers={['22x25']}>Chng ti khng c sn phm bn cn?</Typography.Heading>
              </div>
              <Typography.Text modifiers={['16x18', '400']}>
                Sn phm bn ang cn trn th trng gi qu cao.
              </Typography.Text>
              <Typography.Text modifiers={['16x18', '400']}>
                Hy gip chng ti mt vi thng tin,
                chng ti s lin h cho bn khi tm thy gi tt dnh cho bn.
              </Typography.Text>
              <Typography.Text modifiers={['16x18', '400']}>
                Bn chn chn s hi lng y!
              </Typography.Text>
            </div>
          </Col>
          <Col lg={6} md={12}>
            <FormProvider {...buyForMeMethod}>
              <div className="t-buyForMe_field">
                <Controller
                  name="fullname"
                  control={buyForMeMethod.control}
                  render={({
                    field: { onChange, value },
                    fieldState: { error },
                  }) => (
                    <Input name="fullname" required label="H v tn" type="text" value={value} bordered onChange={onChange} error={error?.message} />
                  )}
                />
              </div>
              <div className="t-buyForMe_field">
                <Controller
                  name="phone"
                  control={buyForMeMethod.control}
                  render={({
                    field: { onChange, value },
                    fieldState: { error },
                  }) => (
                    <Input name="phone" required label="S in thoi" type="text" value={value} bordered onChange={onChange} error={error?.message} />
                  )}
                />
              </div>
              <div className="t-buyForMe_field">
                <Controller
                  name="email"
                  control={buyForMeMethod.control}
                  render={({
                    field: { onChange, value },
                    fieldState: { error },
                  }) => (
                    <Input name="email" required label="Email" type="text" value={value} bordered onChange={onChange} error={error?.message} />
                  )}
                />
              </div>
              <div className="t-buyForMe_field">
                <Controller
                  name="guestNote"
                  control={buyForMeMethod.control}
                  render={({
                    field: { onChange, value },
                    fieldState: { error },
                  }) => (
                    <TextArea name="guestNote" label="Ghi ch" rows={2} value={value} onChange={onChange} error={error?.message} />
                  )}
                />
              </div>
              <div className="t-buyForMe_field">
                <Controller
                  name="productImages"
                  control={buyForMeMethod.control}
                  render={({
                    field: { onChange, value },
                    fieldState: { error },
                  }) => (
                    <>
                      <label
                        htmlFor="productImages"
                        className="t-buyForMe_field_image"
                      >
                        Chn hnh nh
                      </label>
                      <input
                        id="productImages"
                        hidden
                        name="productImages"
                        type="file"
                        multiple
                        accept="image/*"
                        onChange={(e) => {
                          if (e.currentTarget.files) {
                            if (Object.keys(e.currentTarget.files).length > 3) {
                              buyForMeMethod.setError(
                                'productImages',
                                { message: 'Vui lng chn ti a 3 nh' }
                              );
                            } else {
                              onChange(Object.values(e.currentTarget.files));
                            }
                          }
                        }}
                      />
                      {
                        error && (
                          <div className="a-input_error">
                            {error.message}
                          </div>
                        )
                      }
                      {value && value.length > 0 && (
                        <div className="t-buyForMe_images">
                          {value.map((item, idx) => (
                            <Image
                              key={item.name + idx.toString()}
                              imgSrc={URL.createObjectURL(item)}
                              ratio="1x1"
                            />
                          ))}
                        </div>
                      )}
                    </>
                  )}
                />
              </div>
              <div className="t-buyForMe_button">
                <Button
                  variant="dark"
                  sizes="h48"
                  loading={isLoading}
                  handleClick={buyForMeMethod.handleSubmit(onSubmit)}
                >
                  Gi thng tin
                </Button>
              </div>
            </FormProvider>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default BuyForMe;
